import Endpoints from "@/constants/endpoints";
import Helpers from "@/helpers/index";
let urls : URLS = {};

interface urlFunc {
  (params?: {}, customerId?: string) : string;
}

interface URLS {
  [name: string] : urlFunc;
}

Object.keys(Endpoints).forEach(function(key) {
  urls[key.toLowerCase()] = function(params, customerId) {
    let domain = Helpers.getDomain();
    let endPoint = typeof Endpoints[key] == "function" ? Endpoints[key](params) : Endpoints[key];
    let endpointTemplate = domain + endPoint;
    if(params){
      if(Object.keys(params).length > 0) {
        endpointTemplate += "?";
      }
      Object.keys(params).forEach(function(param) {
        endpointTemplate += param + "=" + params[param] + "&";
      });
    }

    return endpointTemplate;
  }
});

export default urls;
