import { PaymentIntent, Options, PaymentInfo, Callbacks, AdditionalData, Gateway } from "@/extensions/three_domain_secure/common/types";
import { PaymentInfo as IDealPaymentInfo } from '@/plugins/payments/iDeal/types'

export enum ComponentType {
  Card = "card",
  Bank = "bank_account"
}

export interface Component {
  name: string;
  type: ComponentType;
  mount(id?): Promise<boolean>;
  tokenize(data?);
  framesCreated(): string[];
  delegateEvent(event): void;
  authorizeWith3ds(paymentIntent: PaymentIntent, additionalData: AdditionalData, callbacks: Callbacks): Promise<PaymentIntent>
}

export interface ThreeDSHandler {
  setPaymentIntent(paymentIntent: PaymentIntent, options: Options)
  updatePaymentIntent(paymentIntent)
  handleCardPayment(paymentInfo: PaymentInfo, callbacks: Callbacks) : Promise<PaymentIntent>
  getPaymentIntent(): PaymentIntent
}

export interface IDealPayment {
  component?: any
  setPaymentIntent(paymentIntent: PaymentIntent, options: Options)
  updatePaymentIntent(paymentIntent: PaymentIntent)
  getPaymentIntent(): PaymentIntent
  mountBankList(id: string) : Promise<any>
  getSelectedBankMeta(): any
  handlePayment(paymentInfo?: IDealPaymentInfo, callbacks?: Callbacks) : Promise<any>
}

export interface SofortPayment {
  setPaymentIntent(paymentIntent: PaymentIntent, options: Options)
  updatePaymentIntent(paymentIntent: PaymentIntent)
  getPaymentIntent(): PaymentIntent
  handlePayment(callbacks?: Callbacks) : Promise<any>
}